import React, { useRef } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

// Import your Art.css stylesheet
import './Art.css';

// import required modules
import { Navigation, Pagination, Mousewheel, Keyboard } from 'swiper/modules';

function Art() {
  const swiperRef = useRef(null); // Create a ref for the Swiper instance

  const videos = [
    { id: 'RTftwYWpTSc', description: 'Blender 3DMMD' },
    { id: 'ysnQedT2g9Y', description: '3DSMAX ' },
    { id: 'R3btI6t1sYM', description: 'Blender 3DMMD' },
    { id: 'VSDr546BJsk', description: 'AfterEffect 3D' },
    { id: 'E84PmQ38Vr4', description: 'Live2D ' },
  ];

  return (
    <div id="art" className="art-container">
      <h1></h1>
      <Swiper
        cssMode={true}
        navigation={true}
        pagination={true}
        mousewheel={true}
        keyboard={true}
        loop={true}
        modules={[Navigation, Pagination, Mousewheel, Keyboard]}
        spaceBetween={50}
        slidesPerView={1}
        centeredSlides={true}
        onSwiper={(swiper) => swiperRef.current = swiper}
        className="mySwiper"
      >
        {videos.map((video, index) => (
          <SwiperSlide key={index}>
            <iframe
              className="art-iframe"
              src={`https://www.youtube.com/embed/${video.id}`}
              title="YouTube video player"
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            ></iframe>
            <p>{video.description}</p>
            <p></p>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
}

export default Art;
